.bg-container {
    background-image: linear-gradient(
      to bottom right,
      #9796f0 0.5%,
      #fbc7d4 150%
    );
    background-size: cover;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    text-align: center;
    padding-bottom: 16px;
  }
  
  .emoji-body {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1;
  }
  
  .emojis-list-container {
    display: flex;
    justify-content: space-between;
    margin: auto;
    flex-wrap: wrap;
    width: 80%;
    max-width: 1000px;
  }